*{
    font-family: 'Poppins', sans-serif;
}



#home-hero{
    background-image: url("../public/img/home/hero-home.jpg");
     background-repeat: no-repeat;
     background-size:cover;
     background-position: center;
}

.CTA{
    background-image: url("../public/img/home/cta-bg.webp");
     background-repeat: no-repeat;
     background-size:cover;
     transition: all .9s;
}
.CTA:hover{
    transform: scale(1.05);
}

#about-hero{
    background-image: url("../public/img/about/about-hero.webp");
     background-repeat: no-repeat;
     background-size:cover;
     background-position: 50% 50%;
}

#hero-main{
    background-image: url("../public/img/gallery/gallery-bg.webp");
     background-repeat: no-repeat;
     background-size:cover;
     background-position: 20% 80%;
}

#contact-hero{
    background-image: url("../public/img/contact/contact-hero.jpg");
     background-repeat: no-repeat;
     background-size:cover;
     background-position:  75% 25%;
}

#mapStyle{
    filter: grayscale(0.1) contrast(1.3) opacity(1);
}

.float-01{
	position:fixed;
	width:60px;
	height:60px;
	bottom:30px;
	left:20px;
	text-align:center;
    font-size:30px;
    z-index:100;
}

.my-float{
	margin-top:16px;
}

